<template>
  <table class="table table-bordered">
    <thead>
      <tr>
        <th>Upload Time</th>
        <th>ID</th>
        <th>User</th>
        <th>File</th>
        <th>Status</th>
        <th>Notes</th>
      </tr>
    </thead>

    <tbody>
      <tr v-for="upload in uploads" :class="{'text-danger': upload.errorMsg}">
        <td>{{ upload.uploadTime }}</td>
        <td>{{ upload.id }}</td>
        <td>{{ upload.user.name }}</td>
        <td>{{ upload.filename }}</td>
        <td>{{ upload.status }}</td>
        <td>{{ upload.errorMsg }}</td>
      </tr>
    </tbody>

  </table>
</template>


<script>
export default {
  props: {
    uploads: {},
  },
}
</script>



