<template>
  <div class="container-fluid">

    <div class="row py-3">
      <div class="col-md-12">
        <h1 class="text-center">Fund</h1>
      </div>
    </div>

    <div class="row">
      <div class="col-md-12">
        <div v-if="totalPortfolios && totalPortfolios.assets">
          <pie-chart :assets="totalPortfolios.assets"></pie-chart>
          <assets-table :coins="totalPortfolios.assets"></assets-table>
        </div>
        <div v-if="(!totalPortfolios || !totalPortfolios.assets.length) && !loading" class="alert alert-danger text-center">No Chart Data</div>

        <h3>ROI</h3>
        <roi-table v-if="totalPortfolios && totalPortfolios.roi" :roi="totalPortfolios.roi"></roi-table>
        <div v-if="!totalPortfolios && !loading" class="alert alert-danger text-center">No ROI Data</div>
      </div>

      <loading :loading="loading"></loading>

    </div>

  </div>
</template>


<script>
import PieChart from '@/components/charts/AssetsPieChart'
import AssetsTable from '@/components/tables/AssetsTable'
import RoiTable from '@/components/tables/RoiTable'

export default {
  components: {
    PieChart,
    AssetsTable,
    RoiTable,
  },
  created() {
    this.loading = true
    this.$store.dispatch('getTotalPortfolios')
      .finally(() => {
        this.loading = false
      })
  },
  data() {
    return {
      loading: false,
    }
  },
  computed: {
    totalPortfolios() {
      return this.$store.getters.getTotalPortfolios
    },
  },
}
</script>



