import axios from 'axios'

import {API_V1_URL} from '@/config'
import usersService from '@/services/usersService'
import dateUtils from '@/helpers/dateUtils'

function apiToUpload(data) {
  return {
    id: data.id,
    status: data.status,
    filename: data.meta.fileName,
    uploadTime: dateUtils.formatDateTime(new Date(data.created)),
    errorMsg: data.output ? data.output.reason : null,
    user: usersService.apiToUser(data.creator),
  }
}

export default {
  uploadCSV(csv) {
    return axios.post(API_V1_URL + '/tx/bulk', csv)
      .then(res => res.data.data)
  },
  getRecentUploadsSummary(offset = 0, limit = 20) {
    return axios.get(API_V1_URL + '/operations?offset=' + offset + '&limit=' + limit)
      .then(res => res.data.data.map(upload => apiToUpload(upload)))
  },
}



